/* src/App.css */
:root {
  --primary: #ff4d5a;
  --primary-dark: #e0444f;
  --dark-bg: #0f0f15;
  --card-bg: #1a1a25;
  --text: #f0f0f0;
  --text-secondary: #b0b0c0;
  --accent: #4d79ff;
  --transition: all 0.3s ease;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background: var(--dark-bg);
  color: var(--text);
  font-family: 'Poppins', sans-serif;
  line-height: 1.6;
  overflow-x: hidden;
}

h1, h2, h3, h4 {
  font-weight: 600;
  line-height: 1.2;
}

a {
  text-decoration: none;
  color: var(--primary);
  transition: var(--transition);
}

img:not([src]),
img[src=""] {
  display: none;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.section {
  padding-top: 100px;
  padding-bottom: 100px;
}

.section-footer {
  padding-top: 100px;
}

.section-title {
  text-align: center;
  margin-bottom: 60px;
  position: relative;
}

.section-title h2 {
  font-size: 2.5rem;
  display: inline-block;
  background: linear-gradient(45deg, var(--primary), var(--accent));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 5px 15px rgba(255, 77, 90, 0.2);
}

.section-title h2::after {
  content: '';
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: linear-gradient(45deg, var(--primary), var(--accent));
  border-radius: 2px;
}

.btn {
  display: inline-block;
  padding: 12px 30px;
  background: linear-gradient(45deg, var(--primary), var(--primary-dark));
  color: white;
  border-radius: 30px;
  font-weight: 500;
  transition: var(--transition);
  box-shadow: 0 5px 15px rgba(255, 77, 90, 0.3);
  border: none;
  cursor: pointer;
  font-size: 1rem;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(255, 77, 90, 0.4);
}

.btn-outline {
  background: transparent;
  border: 2px solid var(--primary);
  color: var(--primary);
  box-shadow: none;
}

.btn-outline:hover {
  background: var(--primary);
  color: white;
}

/* Header */
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  padding: 20px 0;
  transition: var(--transition);
}

header.scrolled {
  background: rgba(15, 15, 21, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  padding: 15px 0;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.8rem;
  font-weight: 700;
  display: flex;
  align-items: center;
}

.logo span {
  color: var(--primary);
}

.nav-links {
  display: flex;
  list-style: none;
}

.nav-links li {
  margin-left: 30px;
}

.nav-links a {
  color: var(--text);
  font-weight: 500;
  position: relative;
  padding: 5px 0;
}

.nav-links a::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--primary);
  transition: var(--transition);
  box-shadow: 0 0 2px var(--primary);
}

.nav-links a:hover::after,
.nav-links a.active::after {
  width: 100%;
}

.nav-links a.active, .nav-links a:hover {
  color: var(--primary);
  text-shadow: 0 0 10px var(--primary);
}

.mobile-menu {
  display: none;
  font-size: 1.5rem;
  cursor: pointer;
}

/* Hero Section */
.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  padding-top: 80px;
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255,77,90,0.1) 0%, rgba(15,15,21,0) 70%);
  z-index: -1;
}

.hero-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 50px;
}

.hero-text {
  flex: 1;
}

.hero-image {
  flex: 1;
  display: flex;
  justify-content: center;
}

.hero-text h1 {
  font-size: 3.5rem;
  margin-bottom: 20px;
  line-height: 1.2;
}

.hero-text h1 span {
  color: var(--primary);
}

.hero-text p {
  font-size: 1.2rem;
  margin-bottom: 30px;
  color: var(--text-secondary);
  max-width: 600px;
}

.hero-btns {
  display: flex;
  gap: 20px;
  margin-top: 40px;
}

.hero-img-container {
  width: 350px;
  height: 350px;
  border-radius: 50%;
  background: linear-gradient(45deg, var(--primary), var(--accent));
  display: flex;
  align-items: center;
  justify-content: center;
  animation: float 6s ease-in-out infinite;
  box-shadow: 0 20px 40px rgba(255, 77, 90, 0.2);
  overflow: hidden;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

.placeholder-avatar {
  width: 95%;
  height: 95%;
  border-radius: 50%;
  background: linear-gradient(45deg, #1a1a25, #2a2a35);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
  font-size: 1rem;
}

.social-icons {
  display: flex;
  gap: 20px;
  margin-top: 30px;
}

.social-icon {
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--card-bg);
  border-radius: 50%;
  font-size: 1.2rem;
  color: var(--text);
  transition: var(--transition);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.social-icon:hover {
  background: var(--primary);
  transform: translateY(-5px);
  color: white;
}

/* About Section */
.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  align-items: center;
}

@media (max-width: 992px) {
  .about-content {
    grid-template-columns: 1fr;
  }
}

.about-text h3 {
  font-size: 1.8rem;
  margin-bottom: 20px;
}

.about-text p {
  margin-bottom: 20px;
  color: var(--text-secondary);
}

.stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 40px;
}

.stat-box {
  background: var(--card-bg);
  padding: 25px;
  border-radius: 10px;
  text-align: center;
  transition: var(--transition);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  opacity: 0;
  transform: translateY(20px);
}

.stat-box.animate {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 0.5s ease, transform 0.5s ease;
}

.stat-box:nth-child(1).animate {
  transition-delay: 0.1s;
}

.stat-box:nth-child(2).animate {
  transition-delay: 0.2s;
}

.stat-box:nth-child(3).animate {
  transition-delay: 0.3s;
}

.stat-box:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
}

.stat-box h4 {
  font-size: 2.5rem;
  color: var(--primary);
  margin-bottom: 10px;
}

.stat-box p {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

/* Skills Visualization */
.skills-visualization {
  background: var(--card-bg);
  border-radius: 15px;
  padding: 30px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.radar-container {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  position: relative;
}

.radar-chart {
  width: 100%;
  height: 400px;
  position: relative;
}

.radar-grid {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 50%;
}

.radar-grid-1 { width: 80%; height: 80%; }
.radar-grid-2 { width: 60%; height: 60%; }
.radar-grid-3 { width: 40%; height: 40%; }
.radar-grid-4 { width: 20%; height: 20%; }

.radar-axis {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 2px;
  width: 45%;
  background: rgba(255, 255, 255, 0.1);
  transform-origin: left center;
}

.radar-label {
  position: absolute;
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.radar-points {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
}

.radar-point {
  position: absolute;
  width: 12px;
  height: 12px;
  background: var(--primary);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 15px var(--primary);
}

.radar-area {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  clip-path: polygon(50% 50%, 50% 20%, 70% 35%, 65% 65%, 50% 70%, 35% 65%, 30% 35%);
  background: linear-gradient(135deg, rgba(255,77,90,0.3), rgba(77,121,255,0.3));
}

.radar-legend {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
}

.legend-item {
  display: flex;
  align-items: center;
  font-size: 0.9rem;
}

.legend-color {
  width: 15px;
  height: 15px;
  border-radius: 3px;
  margin-right: 8px;
  background: var(--primary);
}

/* Skills Section */
.skills-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
}

@media (max-width: 768px) {
  .skills-container {
    grid-template-columns: 1fr;
  }
}

.skill-category {
  margin-bottom: 40px;
}

.skill-category h3 {
  font-size: 1.5rem;
  margin-bottom: 25px;
  display: flex;
  align-items: center;
}

.skill-category h3 i {
  margin-right: 10px;
  color: var(--primary);
}

.skill-item {
  margin-bottom: 25px;
}

.skill-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.skill-bar {
  height: 10px;
  background: #2a2a35;
  border-radius: 5px;
  overflow: hidden;
}

.skill-progress {
  height: 100%;
  background: linear-gradient(90deg, var(--primary), var(--accent));
  border-radius: 5px;
  position: relative;
  width: 0;
  transition: width 1s ease-out;
}

.skill-progress.animate::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent);
  animation: shine 2s infinite;
}

@keyframes shine {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* Projects Section */


.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 30px;
  margin-top: 30px;
}

@media (max-width: 768px) {
  .projects-grid {
    grid-template-columns: 1fr;
  }
}

.project-card {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  overflow: hidden;
  transition: var(--transition);
  box-shadow: var(--box-shadow);
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: var(--box-shadow-hover);
}

.project-img-placeholder {
  height: 200px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.project-main-icon {
  font-size: 4.5rem;
  color: rgba(255, 255, 255, 0.9);
  z-index: 1;
  transition: transform 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.project-card:hover .project-main-icon {
  transform: scale(1.15) rotate(5deg);
  filter: drop-shadow(0 5px 15px rgba(0, 0, 0, 0.3));
}

.project-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.4s ease;
  z-index: 2;
}

.project-card:hover .project-overlay {
  opacity: 1;
}

.overlay-content {
  text-align: center;
  padding: 20px;
  color: white;
}

.overlay-content h3 {
  margin-bottom: 20px;
  font-size: 1.7rem;
  font-weight: 700;
  letter-spacing: -0.5px;
}

.overlay-buttons {
  display: flex;
  gap: 15px;
  justify-content: center;
}

.btn-view {
  background: var(--primary);
  color: white;
  padding: 10px 25px;
  border-radius: 30px;
  font-weight: 600;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
}

.btn-view:hover {
  background: var(--primary-dark);
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(110, 68, 255, 0.35);
}

.btn-icon {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.btn-icon:hover {
  background: var(--accent);
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(0, 217, 192, 0.4);
}

.project-content {
  padding: 25px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.project-header {
  margin-bottom: 15px;
}

.project-header h3 {
  font-size: 1.5rem;
  margin: 0 0 10px 0;
  color: var(--text-primary);
  font-weight: 700;
}

.project-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 15px;
  align-items: baseline;
}

.project-date, .open-source-tag {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 0.8rem;
  padding: 0.5rem 0.5rem;
  border-radius: 50px;
  background: rgba(108, 117, 125, 0.08);
  color: var(--text-secondary);
  font-weight: 500;
  width: 100%;
  height: 2.5rem;
}

.project-date{
  font-size: 0.6rem;
  box-shadow: #0000002e 0px 0px 10px;
}

.meta-icon {
  color: var(--primary);
  font-size: 0.9rem;
}

.open-source-tag {
  background: rgba(9, 255, 226, 0.12);
  color: #008e7e;
  box-shadow: #01ffe12d 0px 0px 10px;
}

.open-source-tag .meta-icon {
  color: #008e7e;
}

.project-description {
  color: var(--text-secondary);
  margin-bottom: 20px;
  flex-grow: 1;
  line-height: 1.6;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 5px;
}

.project-tag {
  padding: 7px 16px;
  border-radius: 50px;
  font-size: 0.85rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.2s ease;
  color: white;
  cursor: default;
}

.project-tag:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
}

/* Filtri */
.projects-filters {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  justify-content: center;
  margin-bottom: 30px;
}

.filter-btn {
  background: rgba(110, 68, 255, 0.08);
  color: var(--primary);
  border: none;
  padding: 10px 22px;
  border-radius: 50px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 0.95rem;
}

.filter-btn:hover {
  background: rgba(110, 68, 255, 0.15);
  transform: translateY(-2px);
}

.filter-btn.active {
  background: var(--primary);
  color: white;
  box-shadow: 0 6px 18px rgba(110, 68, 255, 0.35);
}

/* Contact Section */
.contact-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
}

@media (max-width: 992px) {
  .contact-container {
    grid-template-columns: 1fr;
  }
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.contact-item {
  display: flex;
  align-items: flex-start;
  gap: 15px;
}

.contact-icon {
  width: 50px;
  height: 50px;
  background: var(--card-bg);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  color: var(--primary);
}

.contact-details h3 {
  font-size: 1.2rem;
  margin-bottom: 5px;
}

.contact-details p, .contact-details a {
  color: var(--text-secondary);
}

.contact-form .form-group {
  margin-bottom: 20px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 15px;
  background: var(--card-bg);
  border: 1px solid #2a2a35;
  border-radius: 8px;
  color: var(--text);
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  transition: var(--transition);
}

.contact-form input:focus,
.contact-form textarea:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(255, 77, 90, 0.2);
}

.contact-form textarea {
  min-height: 150px;
  resize: vertical;
}

/* Footer */
footer {
  background: #0a0a0f;
  padding: 50px 0 20px;
}

.footer-content {
  text-align: center;
  margin-bottom: 40px;
}

.footer-logo {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 20px;
}

.footer-logo span {
  color: var(--primary);
}

.footer-socials {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 30px 0;
}

.footer-socials a {
  color: var(--text);
  font-size: 1.2rem;
  transition: var(--transition);
}

.footer-socials a:hover {
  color: var(--primary);
  transform: translateY(-5px);
}

.copyright {
  text-align: center;
  padding-top: 20px;
  border-top: 1px solid #2a2a35;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

/* Responsive */
@media (max-width: 992px) {
  .hero-content {
    flex-direction: column-reverse;
    text-align: center;
  }
  
  .hero-btns, .social-icons {
    justify-content: center;
  }
  
  .hero-text h1 {
    font-size: 3rem;
  }
  
  .hero-img-container {
    width: 280px;
    height: 280px;
  }
}

@media (max-width: 768px) {
  .mobile-menu {
    display: block;
  }
  
  .nav-links {
    position: fixed;
    top: 80px;
    right: -100%;
    flex-direction: column;
    background: var(--dark-bg);
    width: 80%;
    height: calc(100vh - 80px);
    padding: 30px;
    transition: var(--transition);
    box-shadow: -5px 0 15px rgba(0, 0, 0, 0.1);
  }
  
  .nav-links.active {
    right: 0;
  }
  
  .nav-links li {
    margin: 15px 0;
  }
  
  .section {
    padding: 80px 0;
  }
  
  .section-title h2 {
    font-size: 2rem;
  }
  
  .stats {
    grid-template-columns: 1fr;
  }
  
  .hero-text h1 {
    font-size: 2.5rem;
  }
  
  .hero-img-container {
    width: 250px;
    height: 250px;
  }
}

@media (max-width: 480px) {
  .hero-text h1 {
    font-size: 2rem;
  }
  
  .hero-btns {
    flex-direction: column;
    align-items: center;
  }
  
  .hero-img-container {
    width: 220px;
    height: 220px;
  }
  
  .section-title h2 {
    font-size: 1.8rem;
  }
}
