  padding: 25px;
  transition: var(--transition);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

.skill-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.15);
}

.skill-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
}

.skill-icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: white;
}

.skill-bar-container {
  background: #2a2a35;
  border-radius: 5px;
  height: 12px;
  overflow: hidden;
  margin-bottom: 15px;
}

.skill-bar {
  height: 100%;
  position: relative;
  border-radius: 5px;
}

.skill-level {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0.8rem;
  font-weight: 600;
  color: white;
}

.skill-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.skill-tag {
  padding: 5px 12px;
  font-size: 0.8rem;
  background: #4d79ff;
  color: white;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  border-radius: 8px;
}

.skill-badge {
  position: relative;
  float: right;
  display: inline-flex;
  top: 10%;
  align-items: center;
  gap: 4px;
  padding: 2px 6px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  color: #fff;
  border-radius: 12px;
  padding: 4px 7px;
}

.skill-badge.new {
  background-color: #a72828;
}

.skill-badge.active {
  background-color: #b8ad17;
}

/* Project Details */
.project-detail {
  padding-top: 150px;
  min-height: 100vh;
}

.back-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 30px;
  color: var(--primary);
  font-weight: 500;
}

.back-btn:hover {
  text-shadow: 0 0 5px var(--primary);
  transform: scale(105%);
}

.project-header {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  margin-bottom: 50px;
}

@media (max-width: 992px) {
  .project-header {
    grid-template-columns: 1fr;
  }
}

.project-meta h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.project-dates {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
  color: var(--text-secondary);
}

.project-links {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 30px;
}

.project-link {
  padding: 0.2rem 1rem;
  background: var(--card-bg);
  border-radius: 30px;
  display: flex;
  align-items: center;
  gap: 8px;
  white-space: nowrap;
  transition: var(--transition);
}

.project-link:hover {
  background: var(--primary);
  color: white;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.project-tag {
  padding: 8px 15px;
  border-radius: 30px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.9rem;
  color: white;
}

.project-tag.open-source {
  background: #6a0dad;
}

.project-image-main img {
  width: 100%;
  border-radius: 15px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
}

.project-tabs {
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  border-bottom: 1px solid #2a2a35;
  padding-bottom: 15px;
}

.tab-btn {
  padding: 10px 20px;
  background: transparent;
  border: none;
  color: var(--text-secondary);
  font-weight: 500;
  cursor: pointer;
  position: relative;
}

.tab-btn.active {
  color: var(--primary);
}

.tab-btn.active::after {
  content: '';
  position: absolute;
  bottom: -16px;
  left: 0;
  width: 100%;
  height: 3px;
  background: var(--primary);
}

.project-overview h3,
.project-technologies h3,
.project-achievements h3 {
  font-size: 1.8rem;
  margin-bottom: 20px;
}

.project-features,
.project-challenges {
  margin-bottom: 40px;
}

.project-features h4,
.project-challenges h4 {
  font-size: 1.4rem;
  margin: 15px 0;
  color: var(--primary);
}

.project-features ul,
.project-challenges ul {
  padding-left: 20px;
  list-style-type: none;
}

.project-features li,
.project-challenges li {
  margin-bottom: 10px;
  position: relative;
  padding-left: 20px;
}

.project-features li::before {
  content: '✓';
  position: absolute;
  left: 0;
  color: var(--primary);
}

.project-challenges li::before {
  content: '•';
  position: absolute;
  left: 0;
  color: var(--primary);
}

.tech-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 25px;
}

.tech-card {
  background: var(--card-bg);
  border-radius: 15px;
  padding: 25px;
  text-align: center;
}

.tech-icon {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
  font-size: 1.8rem;
  color: white;
}

.achievements-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;
}

.achievement-card {
  display: flex;
  gap: 20px;
  background: var(--card-bg);
  border-radius: 15px;
  padding: 25px;
  align-items: center;
}

.achievement-icon {
  font-size: 2rem;
  color: var(--primary);
}

.achievement-metric {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--primary);
  margin-top: 10px;
}

.project-navigation {
  display: flex;
  justify-content: space-between;
  margin: 50px 0;
  padding-top: 30px;
  border-top: 1px solid #2a2a35;
}

.nav-btn {
  padding: 12px 25px;
  background: var(--card-bg);
  border-radius: 30px;
  color: var(--text);
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  gap: 10px;
}

.nav-btn:hover {
  background-color: var(--primary);
  color: white;
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.nav-btn.prev::before {
  content: '←';
}

.nav-btn.next::after {
  content: '→';
}

.next{
  margin-left: auto;
}

/* Project Gallery */
.project-gallery {
  margin-bottom: 50px;
}

.gallery-main {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
}

.gallery-image-container {
  flex: 1;
  height: 500px;
  border-radius: 15px;
  overflow: hidden;
}

.gallery-image-container img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.gallery-nav {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--card-bg);
  border: none;
  color: var(--text);
  font-size: 1.2rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gallery-thumbnails {
  display: flex;
  gap: 15px;
  overflow-x: auto;
  padding: 15px 0;
  margin-bottom: 20px;
}

.thumbnail {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  opacity: 0.6;
  transition: var(--transition);
  flex-shrink: 0;
}

.thumbnail.active {
  opacity: 1;
  box-shadow: 0 0 20px 0 var(--primary);
}

.thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.gallery-counter {
  text-align: center;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

/* Scroll to top */
.scroll-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--primary);
  color: white;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0;
  transform: translateY(20px);
  transition: var(--transition);
  box-shadow: 0 0px 15px rgba(255, 77, 90, 0.3);
  z-index: 1000;
}

.scroll-to-top.visible {
  opacity: 1;
  transform: translateY(0);
}

.scroll-to-top:hover {
  transform: translateY(-10px);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr;
  }
  
  .project-header {
    grid-template-columns: 1fr;
  }
  
  .gallery-image-container {
    height: 300px;
  }
}